
import React from 'react';
import logo from './logo.svg';
import './App.css';
import 'antd/dist/antd.css';
import { Descriptions, Spin } from 'antd';
import { FirebaseApp, initializeApp } from 'firebase/app';
import { Firestore, getFirestore, collection, addDoc } from 'firebase/firestore/lite';

class GeolocationDisplayProps {
  firestore?: Firestore;
}

class GeolocationDisplayState {
  currentPosition?: GeolocationPosition = undefined;
  watchId = 0;
}

class GeolocationDisplay extends React.Component<GeolocationDisplayProps, GeolocationDisplayState> {
  constructor(props: GeolocationDisplayProps) {
    super(props);
    this.state = new GeolocationDisplayState();
  }

  async pushPositionToFirestoreIfAvailable(newPosition: GeolocationPosition) {
    // if (this.props.firestore) {
    //   let col = collection(this.props.firestore, "positions");
    //   let coords = newPosition.coords;
    //   let result = await addDoc(col, {
    //     accuracy: coords.accuracy,
    //     altitude: coords.altitude,
    //     altitudeAccuracy: coords.altitudeAccuracy,
    //     heading: coords.heading,
    //     latitude: coords.latitude,
    //     longitude: coords.longitude,
    //     speed: coords.speed,
    //     timestamp: newPosition.timestamp,
    //   });

    //   console.log(`Successfully pushed document with id = ${result.id} to database with path = ${result.path}`);
    // }
  }

  updatePosition(newPosition: GeolocationPosition) {
    this.setState({
      currentPosition: newPosition
    });

    this.pushPositionToFirestoreIfAvailable(newPosition);
  }

  componentDidMount() {
    console.log(`Called ${this.constructor.name}.componentDidMount`);
    this.setState({
      watchId: navigator.geolocation.watchPosition(
        this.updatePosition.bind(this),
        console.error,
        {
          enableHighAccuracy: true,
          timeout: 10000,
        }
      )
    });
  }

  componentWillUnmount() {
    console.log(`Called ${this.constructor.name}.componentWillUnmount`);
    let watchId = this.state.watchId;
    if (watchId) {
      navigator.geolocation.clearWatch(watchId);
      this.setState({
        watchId: 0,
      });
    }
  }

  // update(geolocation: Geolocation) {
  //   this.setState(geolocation);
  // }

  render() {
    let pos = this.state?.currentPosition

    if (!pos) {
      return (
        <Spin />
      );
    } else {
      let coords = pos.coords;

      return (
        <Descriptions title="Geolocation">
          <Descriptions.Item label="Timestamp">{pos.timestamp}</Descriptions.Item>
          <Descriptions.Item label="Latitude">{coords.latitude}</Descriptions.Item>
          <Descriptions.Item label="Longitude">{coords.longitude}</Descriptions.Item>
          <Descriptions.Item label="Altitude">{coords.altitude}</Descriptions.Item>
          <Descriptions.Item label="Location Accuracy">{coords.accuracy}</Descriptions.Item>
          <Descriptions.Item label="Altitude Accuracy">{coords.altitudeAccuracy}</Descriptions.Item>
          <Descriptions.Item label="Heading">{coords.heading}</Descriptions.Item>
          <Descriptions.Item label="Speed">{coords.speed}</Descriptions.Item>
        </Descriptions>
      );
    }
  }
}

